import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'



function Country({ match }) {

    const [country, setCountry] = useState([])

    const fetchCountry = async () => {
        const response = await fetch(`https://restcountries.eu/rest/v2/name/${match.params.name}`)
        const item = await response.json()
        setCountry(item)
    }

    useEffect(() => {
        fetchCountry()
    }, [])

    return (
        <section className='flexbox'>
            <Link to='/'>
                <button><i className="fas fa-chevron-circle-left"></i> Back</button>
            </Link>
            {country.map((c) => {
                const { numericCode, flag, name, nativeName, population
                    , region, subregion, capital, topLevelDomain,
                    currencies, languages, borders } = c

                return (
                    <article key={numericCode}>
                        <img src={flag} alt="Nation-img" />
                        <div className="details">
                            <div className="title-div">
                                <h2>{name}</h2>
                                <h4>Native Name : <span>{nativeName}</span></h4>
                                <h4>Population : <span>{population}</span></h4>
                                <h4>Region : <span>{region}</span></h4>
                                <h4>Sub Region : <span>{subregion}</span></h4>
                                <h4>Capital : <span>{capital}</span></h4>
                            </div>
                            <div className="info-div">
                                <h4>Top Level Domain: <span>{topLevelDomain[0]}</span></h4>
                                <h4>Currencies: <span>{currencies[0].name}</span></h4>
                                <h4>Languages: <span>{languages[0].name}</span></h4>
                            </div>
                        </div>
                    </article>
                )
            })}
        </section>
    )
}

export default Country

