import React from 'react'

function Header() {
    const themeChnager = () =>{
        document.body.classList.toggle('dark')
    }
    return (
        <header className='header'>
            <div>
                <h1>Where in the world?</h1>
            </div>
            <div id='switcher' onClick={()=>themeChnager()}>
                <i className="fas fa-moon"></i> Dark Mode
            </div>
        </header>
    )
}

export default Header
