import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const url = 'https://restcountries.eu/rest/v2/all'

function Countries({ cname,rname }) {
    const [countries, setCountries] = useState([])

    const fetchCountryData = async () => {
        const response = await fetch(url)
        const countries = await response.json()
        setCountries(countries)
    }

    useEffect(() => {
        fetchCountryData()
    }, [])

    if (cname === '' && rname==='') {
        return (
            <section className='grid'>

                {
                    countries.map((country, idx) => {
                        const { name, population, region, capital, flag, numericCode } = country

                        return (
                            <Link to={`/countries/${name}`} style={{ textDecoration: "none" }} key={idx}>
                                <article key={numericCode} className='country-card'>
                                    <img src={flag} alt="Nation-img" />
                                    <div className="details">
                                        <h3>Country : {name}</h3>
                                        <h4>Population :{population}</h4>
                                        <h4>Region :{region}</h4>
                                        <h4>Capital :{capital}</h4>
                                    </div>
                                </article>
                            </Link>
                        )
                    }
                    )
                }
            </section>
        )
    }
    if (rname !== '') {
        const searchedCountry = countries.filter(country => country.region.toLowerCase() === rname.toLowerCase())
        return (
            <section className='grid'>
                {
                    searchedCountry.map((filteredCountry,idx) => {
                        const { name, population, region, capital, flag, numericCode } = filteredCountry
                        return (
                            <Link to={`/countries/${name}`} style={{ textDecoration: "none" }} key={idx}>
                                <article key={numericCode} className='country-card'>
                                    <img src={flag} alt="Nation-img" />
                                    <div className="details">
                                        <h3>Country : {name}</h3>
                                        <h4>Population :{population}</h4>
                                        <h4>Region :{region}</h4>
                                        <h4>Capital :{capital}</h4>
                                    </div>
                                </article>
                            </Link>
                        )
                    }
                    )
                }
            </section>
        )
    } else {
        const searchedCountry = countries.filter(country => country.name.toLowerCase() === cname.toLowerCase())
        return (
            <section className='grid'>
                {
                    searchedCountry.map((filteredCountry,idx) => {
                        const { name, population, region, capital, flag, numericCode } = filteredCountry
                        return (
                            <Link to={`/countries/${name}`} style={{ textDecoration: "none" }} key={idx}>
                                <article key={numericCode} className='country-card'>
                                    <img src={flag} alt="Nation-img" />
                                    <div className="details">
                                        <h3>Country : {name}</h3>
                                        <h4>Population :{population}</h4>
                                        <h4>Region :{region}</h4>
                                        <h4>Capital :{capital}</h4>
                                    </div>
                                </article>
                            </Link>
                        )
                    }
                    )
                }
            </section>
        )
    }
}

export default Countries
